<template>
  <div class="container">
    <h2>Secret Management</h2>
    <!-- <div class="card card-inverse" style="background-color: #333; border-color: #333;">
      <div class="card-block">
        <h3 class="card-title" v-bind="userId"></h3>
        <p class="card-text" v-bind="secret"></p>
        <button class="btn btn-primary" @click="getSecrets">Get Secrets</button>
      </div>
    </div> -->
    <!-- Data block -->
    <cards :secretsList="secretsList"/>
  </div>
</template>

<script>
import Cards from '@/components/Cards.vue'
export default {
  name: 'SecretsManage',
  // props: {
  //   userId: String,
  //   secret: String
  // },
  components: {
    'cards': Cards
  },
  methods: {
    async getSecrets () {
      await this.$store.dispatch('fetchSecrets')
    }
  },
  async mounted () {
    await this.$store.dispatch('fetchSessions')
    await this.$store.dispatch('fetchSecrets')
    if (!this.$store.state.authUser) {
      window.location.href = '/login'
    }
  },
  computed: {
    secretsList () {
      return this.$store.state.secretsList
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
body {
  padding-top: 5rem;
}
.starter-template {
  padding: 3rem 1.5rem;
  text-align: center;
}
</style>



