<template>
  <!-- Navbar Content -->
  <nav class="navbar navbar-toggleable-md navbar-inverse bg-inverse fixed-top">
    <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <!-- WebSite logo -->
    <router-link class="navbar-brand" to="/">OTA Admin</router-link>
    <!-- Function List -->
    <div class="collapse navbar-collapse" id="navbarsExampleDefault">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item">
          <router-link class="nav-link" to="/">Home</router-link>
        </li>
        <li class="nav-item">
          <router-link class="nav-link" to="/secretsMng">Secrets Manage</router-link>
        </li>
        <li class="nav-item" v-if="!this.$store.state.authUser">
          <router-link class="nav-link" to="/login">Login</router-link>
        </li>
        <li class="nav-item" v-if="this.$store.state.authUser">
          <!-- <router-link class="nav-link" to="/">Logout</router-link> -->
          <div class="logout" @click="logout">Logout</div>
        </li>
        <!-- <li class="nav-item dropdown">
          <router-link class="nav-link dropdown-toggle" to="http://example.com" id="dropdown01" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Dropdown</router-link>
          <div class="dropdown-menu" aria-labelledby="dropdown01">
            <router-link class="dropdown-item" to="/">Action</router-link>
            <router-link class="dropdown-item" to="/">Another action</router-link>
            <router-link class="dropdown-item" to="/">Something else here</router-link>
          </div>
        </li> -->
      </ul>
    </div>
  </nav>
</template>

<script>
export default {
  name: 'NavBar',
  data () {
    return {
      msg: 'OTA Server Admin Content'
    }
  },
  methods: {
    async logout () {
      try {
        await this.$store.dispatch('logout')
      } catch (e) {
        console.log(e.message)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
body {
  padding-top: 5rem;
}
.starter-template {
  padding: 3rem 1.5rem;
  text-align: center;
}
.nav-item .logout {
  color: white;
  /* display: flex; */
  display: block;
  padding: .5em 0em;
  margin-left: 0;
}
</style>



