import Vue from 'vue'
import Router from 'vue-router'
// import App from '@/App'
import Home from '@/components/Home'
import SecretsManage from '@/components/SecretsManage'
import Login from '@/components/Login'
Vue.use(Router)
const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/secretsMng',
      name: 'SecretsManage',
      component: SecretsManage
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ],
  mode: 'history',
  base: '/'
})
export default router



