<template>
  <div class="modal modal-static fade" id="myModal" data-backdrop="static" data-keyboard="false">
    <div class="modal-dialog">
      <div class="modal-content">
        <!-- Modal Header -->
        <div class="modal-header">
          <h4 class="modal-title"></h4>
          <button class="close" data-dismiss="modal">&times;</button>
        </div>
        <!-- Modal Body -->
        <div class="modal-body">
          <form class="upsertContent">
            <input type="hidden" name="userId"/>
            <input type="hidden" name="keyNum"/>
            <label for="secret">Secret:</label><input type="text" class="form-control" name="secret" placeholder="secret">
          </form>
        </div>
        <!-- Modal footer -->
        <div class="modal-footer">
          <button class="btn btn-primary" @click="doSave">Save</button>
          <button class="btn btn-secondary" data-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import jQuery from 'jquery'
window.$ = window.jQuery = jQuery
// const host = `http://localhost:7000`
const host = ''
export default {
  name: 'Modal',
  methods: {
    async doSave () {
      let userId = window.$(`form.upsertContent input[name='userId']`).val()
      let keyNum = window.$(`form.upsertContent input[name='keyNum']`).val()
      let secret = window.$(`form.upsertContent input[name='secret']`).val()
      let token = this.$store.state.token
      // console.log(userId, keyNum, secret)
      const {data} = await axios.post(`${host}/api/modifySecret`, {
        userId: userId,
        keyNum: keyNum,
        secret: secret,
        token: token
      })
      console.log(data.data[0])
      await this.$store.dispatch('setToken', data.data[0])
      await this.$store.dispatch('fetchSecrets')
      window.$('#myModal').modal('hide')
    }
  }
}
</script>



