import Vuex from 'vuex'
import Vue from 'vue'
import VueSession from 'vue-session'
import axios from 'axios'
// import VueSessionStorage from 'vue-sessionstorage'
// const host = 'http://localhost:7000'
const host = ''
Vue.use(Vuex)
Vue.use(VueSession)
const keyNum = 1
// Vue.use(VueSessionStorage)
const createStore = () => {
  return new Vuex.Store({
    // initalizerAction: 'loadData',
    actions: {
      // for login
      async login ({commit}, {username, passwd}) {
        console.log(username, passwd)
        try {
          const {data} = await axios.post(`${host}/api/user`, {
            // headers: {'Access-Control-Allow-Origin': '*'},
            username,
            passwd,
            number: keyNum
          }).then(response => {
            // console.log(this.$session)
            return response
          })
          window.localStorage.setItem('username', username)
          commit('SET_USER', data)
          commit('SET_TOKEN', data)
        } catch (error) {
          // if user login
          if (error.response && error.response.status === 401) {
            throw new Error('Bad credentiasl')
          }
          if (error.response && error.response.status === 403) {
            throw new Error('Need to Activated')
          }
          console.log(error.message)
          throw error
        }
      },
      // for logout
      async logout ({commit}) {
        await axios.post(`${host}/api/logout`)
        window.localStorage.removeItem('username')
        commit('SET_USER', null)
      },
      async fetchSecrets ({commit}) {
        console.log(this.state.token)
        let token = this.state.token
        const {data} = await axios.get(`${host}/api/getAllSecrets`, {
          headers: {
            'x-access-token': token
          }
        })
        console.log(data.data)
        let secretList = data.data
        commit('SET_SECRETS_LIST', secretList)
      },
      async setToken ({commit}, data) {
        commit('SET_TOKEN', data)
      },
      async fetchSessions ({commit}) {
        let username = window.localStorage.getItem('username') || null
        // console.log(username)
        const {data} = await axios.post(`${host}/api/sessionsStatus`, {username: username})
        let {session} = data
        // console.log(session)
        commit('SET_SESSION', session)
        commit('SET_USER', session)
        if (session) {
          let {token} = session
          console.log(token)

          commit('SET_TOKEN', session)
        }
      }
    },
    state: {
      authUser: null,
      token: null,
      registerStatus: null,
      isProcessing: false,
      isDialogShow: false,
      dialogMsg: null,
      session: null,
      secretsList: null
    },
    mutations: {
      SET_USER: (state, authUser) => {
        state.authUser = authUser
      },
      SET_TOKEN: (state, {token}) => {
        state.token = token
      },
      SET_REGISTER_STATUS: (state, status) => {
        state.registerStatus = status
      },
      SET_SESSION: (state, session) => {
        state.session = session
      },
      SET_SECRETS_LIST: (state, secretsList) => {
        state.secretsList = secretsList
      }
    },
    getters: {
      getSecrets: (state) => {
        return state.secretsList
      }
    }
  })
}

export default createStore



