<template>
  <div class="container">
    <form v-if="!this.isLogin" @submit.prevent="login" class="form-signin">
      <p class="error" v-if="formError">{{formError}}</p>
      <h2 class="form-signin-heading">Please sign in</h2>
      <label for="username" class="sr-only">Username</label>
      <input type="text" v-model="formUsername" class="form-control" name="username" placeholder="User name" required autofocus>
      <label for="passwd" class="sr-only">Password</label>
      <input type="password" v-model="formPasswd" class="form-control" name="passwd" placeholder="Password" required>
      <div class="checkbox">
        <label>
          <input type="checkbox" value="remember-me"> Remember me
        </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block">Sign in</button>
    </form>
  </div>
</template>
<script>
export default {
  name: 'Login',
  computed: {
    isLogin: function () {
      return this.$store.state.authUser
    }
  },
  data () {
    return {
      formError: null,
      formUsername: null,
      formPasswd: null
    }
  },
  methods: {
    async login () {
      console.log(this.formUsername, this.formPasswd)
      try {
        await this.$store.dispatch('login', {
          username: this.formUsername,
          passwd: this.formPasswd
        })
        this.formUsername = null
        this.formPasswd = null
        if (this.$store.state.authUser) {
          window.location.href = '/secretsMng'
        }
      } catch (e) {
        console.log(e)
        this.formError = e.message
      }
    }
  }
}
</script>
<style>
  .form-signin {
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
    border: 1px solid gray;
    border-radius: 5px;
  }
  .form-signin .form-signin-heading,
  .form-signin .checkbox {
    margin-bottom: 10px;
  }
  .form-signin .checkbox {
    font-weight: normal;
  }
  .form-signin .form-control {
    position: relative;
    height: auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 10px;
    font-size: 16px;
  }
  .form-signin .form-control:focus {
    z-index: 2;
  }
  .form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
</style>



