<template>
  <div class="card card-inverse">
    <div :id="'card'+index" class="card-block" :key="index" v-for="(card,index) in secretsList" :secretsList="secretsList" style="background-color: rgb(51, 51, 51); border-color: rgb(51, 51, 51);">
      <div class="inLine"><label for="userId">userId:</label><div class="card-title" name="userId">{{card.userId}}</div></div>
      <div class="inLine"><label for="keyNum">keyNum:</label><div class="card-title" name="keyNum">{{card.keyNum}}</div></div>
      <div class="inLine"><label for="secret">secret:</label><div class="card-title" name="secret">{{card.secret}}</div></div>
      <div class="inLine"><label for="time">time:</label><div class="card-title" name="time">{{new Date(card.modifiedDate).toLocaleString()}}</div></div>
      <button class="btn btn-primary" @click="openModal(index)">Modify Secrets</button>
    </div>
  </div>
</template>
<script>
import jQuery from 'jquery'
window.$ = window.jQuery = jQuery
export default {
  name: 'Cards',
  props: {
    secretsList: {}
  },
  methods: {
    openModal (index) {
      console.log(index)
      window.$('#myModal .modal-title').text('Modify Secret')
      this.setupModal(index)
      window.$('#myModal').modal('show')
    },
    setupModal (index) {
      const targetId = 'card' + index
      const userId = window.$(`#${targetId} .card-title[name='userId']`).text()
      const keyNum = window.$(`#${targetId} .card-title[name='keyNum']`).text()
      const secret = window.$(`#${targetId} .card-title[name='secret']`).text()
      window.$(`.upsertContent input[name='userId']`).val(userId)
      window.$(`.upsertContent input[name='keyNum']`).val(keyNum)
      window.$(`.upsertContent input[name='secret']`).val(secret)
    }
  }
}
</script>
<style lang="css">
  .inLine > * {
    display: inline;
    margin-right: 10px;
  }
  .inLine {
    min-width: 300px;
  }
  .card-block {
    margin-bottom: 10px;
    border-radius: 10px;
    /* min-width: 400px; */
  }
  .card-inverse {
    border: none !important;
  }
</style>



